import { profileAPI } from "../API/api";

const ADD_POST = 'ADD-POST';
const CHANGE_POSTS_TEXTAREA = 'CHANGE-POSTS-TEXTAREA';
const GET_USER_PROFILE = 'GET_USER_PROFILE';
const SET_USER_STATUS = 'SET_USER_STATUS'
const SET_USER_PHOTO = 'SET_USER_PHOTO'

let initialState = {
  profile: null,
  postsData: [
    {
      id: 1,
      message: 'First post',
      likesCount: 20,
      imgSrc: "https://avatars.mds.yandex.net/i?id=17e4dbcf803456717e7b73d1f3a850e6-5008975-images-thumbs&n=13"
    },
    {
      id: 2,
      message: 'Second post',
      likesCount: 48,
      imgSrc: "https://avatars.mds.yandex.net/i?id=17e4dbcf803456717e7b73d1f3a850e6-5008975-images-thumbs&n=13"
    },
  ],
  textAreaPost: '',
  status: null,
}

export const profileReducer = (state = initialState, action) => {

  switch (action.type) {
    case ADD_POST:
      let newPost = {
        id: 3,
        message: state.textAreaPost,
        likesCount: 0,
        imgSrc: "https://avatars.mds.yandex.net/i?id=17e4dbcf803456717e7b73d1f3a850e6-5008975-images-thumbs&n=13",
      }
      return {
        ...state,
        postsData: [...state.postsData, newPost],
        textAreaPost: ''
      };

    case CHANGE_POSTS_TEXTAREA:
      return {
        ...state,
        textAreaPost: action.inputText
      };

    case GET_USER_PROFILE:
      return {
        ...state,
        profile: action.profile
      }

    case SET_USER_STATUS:
      return {
        ...state,
        status: action.status
      }

    case SET_USER_PHOTO:
      return {
        ...state,
        profile: {
          ...state.profile,
          photos: action.photos,
        }
      }

    default: return state;
  }
}

export const addPost = () => ({ type: ADD_POST });
export const changePostTextArea = (text) => ({ type: CHANGE_POSTS_TEXTAREA, inputText: text });
const getUserProfile = (profile) => ({ type: GET_USER_PROFILE, profile });
const setUserStatus = (status) => ({ type: SET_USER_STATUS, status });
const setUserPhoto = (photos) => ({ type: SET_USER_PHOTO, photos });

export const getUser = (userId) => async (dispatch) => {
  const response = await profileAPI.getUserProfile(userId);
  dispatch(getUserProfile(response.data));
}

export const getStatus = (userId) => async (dispatch) => {
  const response = await profileAPI.getStatus(userId);
  dispatch(setUserStatus(response.data));
}

export const updateStatus = (status) => async (dispatch) => {
  const response = await profileAPI.updateStatus(status);
  if (response.data.resultCode === 0) {
    dispatch(setUserStatus(status));
  }
}

export const setPhoto = (photo) => async (dispatch) => {
  const response = await profileAPI.setPhoto(photo);
  if (response.data.resultCode === 0) {
    dispatch(setUserPhoto(response.data.data.photos));
  }
}

export const setInfo = (profile, setError) => async (dispatch, getState) => {
  const userId = getState().auth.userData.userId;
  const response = await profileAPI.setInfo(profile);
  if (response.data.resultCode === 0) {
    dispatch(getUser(userId));
  } else {
    setError('serverError', { type: 'response', message: response.data.messages[0] })
    return Promise.reject();
  }
}

