import { getAuthorization } from "./authReducer";

const INITIAL_SUCCESS = 'INITIAL_SUCCESS';

let initialState = {
  initialized: false,
};

export const appReducer = (state = initialState, action) => {
  switch (action.type) {
    case INITIAL_SUCCESS:
      return {
        ...state,
        initialized: true
      }
    default: return state;
  }
}

const initialSuccess = () => ({ type: INITIAL_SUCCESS })

export const initializeApp = () => (dispatch) => {
  let promiseAuth = dispatch(getAuthorization());

  Promise.all([promiseAuth])
    .then(() => dispatch(initialSuccess()));
}
