const ADD_MESSAGE = 'ADD-MESSAGE';
const CHANGE_MESSAGE_TEXTAREA = 'CHANGE-MESSAGE-TEXTAREA';

let initialState = {
  messagesData: [
    {
      id: 2,
      message: 'Meow Meow?',
      owner: 'Mika',
      imgSrc: "https://i.pinimg.com/originals/5b/1a/9a/5b1a9ab141ba1ade4ab06c8215059225.jpg"
    },
    {
      id: 3,
      message: 'Hi, i\'m fine!',
      owner: 'Me',
      imgSrc: "https://avatars.mds.yandex.net/i?id=17e4dbcf803456717e7b73d1f3a850e6-5008975-images-thumbs&n=13"
    },
    {
      id: 4,
      message: 'Meow',
      owner: 'Mika',
      imgSrc: "https://i.pinimg.com/originals/5b/1a/9a/5b1a9ab141ba1ade4ab06c8215059225.jpg"
    },
  ],
  textAreaMessage: ''
}

export const chatReducer = (state = initialState, action) => {

  switch (action.type) {
    case ADD_MESSAGE:
      let newMessage = {
        id: 8,
        message: state.textAreaMessage,
        owner: 'Me',
        imgSrc: "https://avatars.mds.yandex.net/i?id=17e4dbcf803456717e7b73d1f3a850e6-5008975-images-thumbs&n=13"
      }

      return {
        ...state,
        textAreaMessage: '',
        messagesData: [...state.messagesData, newMessage]
      }

    case CHANGE_MESSAGE_TEXTAREA:
      return {
        ...state,
        textAreaMessage: action.inputText,
      };


    default: return state
  }
}

export const addMessageActionCreator = () => ({ type: ADD_MESSAGE });
export const changeMessageTextAreaActionCreator = (text) =>
  ({ type: CHANGE_MESSAGE_TEXTAREA, inputText: text });
