import React, { lazy, Suspense } from 'react';
import styles from './content.module.css';
import { Routes, Route } from 'react-router-dom';
import Preloader from '../common/Preloder/Preloader';

const ProfileComponent = lazy(() => import('./Profile/ProfileContainer'));
const DialogsComponent = lazy(() => import('./Dialogs/DialogsContainer'));
const UsersContainer = lazy(() => import('./Users/UsersContainer'));
const Music = lazy(() => import('./Music/Music'));
const News = lazy(() => import('./News/News'));
const Settings = lazy(() => import('./Settings/Settings'));
const Login = lazy(() => import('../Login/Login'));

export default function Content(props) {
  return (
    <main className={styles.content}>
      <Suspense fallback={<Preloader />}>
        <Routes>
          <Route path='/profile/:userId?' element={<ProfileComponent />} />
          <Route path='/dialogs/*' element={<DialogsComponent />} />
          <Route path='/users' element={<UsersContainer />} />
          <Route path='/music' element={<Music />} />
          <Route path='/news' element={<News />} />
          <Route path='/settings' element={<Settings />} />
          <Route path='/login' element={<Login />} />
        </Routes>
      </Suspense>
    </main>
  );
};
