import { authAPI } from "../API/api";

const SET_USER_DATA = 'auth/SET_USER_DATA';

let initialState = {
  userData: {
    userId: null,
    email: null,
    login: null,
    isAuth: false
  },
  isFetching: false,
};

export const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATA:
      return {
        ...state,
        userData: {
          ...action.userData,
        }
      }
    default: return state;
  }
}

const setUserData = (userId, email, login, isAuth) => ({
  type: SET_USER_DATA, userData: {
    userId, email, login, isAuth
  }
})

export const getAuthorization = () => async (dispatch) => {
  const response = await authAPI.getAuth()
  if (response.data.resultCode === 0) {
    let { id, email, login } = response.data.data;
    dispatch(setUserData(id, email, login, true));
  }
}


export const login = (email, password, rememberMe, setError) => async (dispatch) => {
  const response = await authAPI.login(email, password, rememberMe)
  if (response.data.resultCode === 0) {
    dispatch(getAuthorization())
  } else {
    setError('serverError', { type: 'response', message: response.data.messages[0] })
  }
}

export const logout = () => async (dispatch) => {
  const response = await authAPI.logout();
  if (response.data.resultCode === 0) {
    dispatch(setUserData(null, null, null, false));
  }
}

